<script>
  import { createEventDispatcher } from "svelte";
  import { onMount } from "svelte";

  export let label = "";
  export let colorHue = 180;
  export let isActive = false;

  onMount(() => {
    setButtonColors(isActive);
  });

  const dispatch = createEventDispatcher();

  function dispatchType() {
    dispatch("dispatchType", {
      selectedType: label
    });
  }

  function setButtonColors(buttonActive) {
    if (buttonActive) {
      document
        .getElementById(label + "-button")
        .style.setProperty(
          "background-color",
          "hsl(" + colorHue + ", 50%, 80%)"
        );
      document
        .getElementById(label + "-button")
        .style.setProperty("color", "hsl(" + colorHue + ", 65%, 40%)");

      document
        .getElementById(label + "-label")
        .style.setProperty("color", "hsl(" + colorHue + ", 65%, 40%)");
    } else {
      document
        .getElementById(label + "-button")
        .style.setProperty("background-color", "#edf2f7");
      document
        .getElementById(label + "-button")
        .style.setProperty("color", "#cbd5e0");

      document
        .getElementById(label + "-label")
        .style.setProperty("color", "#cbd5e0");
    }
  }

  $: if (document.getElementById(label + "-button")) {
    setButtonColors(isActive);
  }
</script>

<style type="text/postcss">.wrapper{display:flex;flex-direction:column;align-items:center;justify-content:center;width:33.33333%;margin-top:1rem;transition-timing-function:ease-in;transition:all .35s}.type-button{border-radius:9999px;padding:.5rem;fill:currentColor;background-color:#edf2f7;color:#cbd5e0}.type-button svg{width:2rem;height:2rem}</style>

<div class="wrapper">
  <button
    class="type-button"
    title={label}
    id="{label}-button"
    on:click={() => dispatchType()}>
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
      <slot />
    </svg>
  </button>
  <span class="text-gray-400" id="{label}-label">{label}</span>
</div>
